import {Stack,Typography,Button,Box} from '@mui/material'
import { ScrollReveal } from "reveal-on-scroll-react";


export const Frontpage1 =() => {
    return(
        <Stack p={3}  spacing={3} px={{sm:0 , md:7}} bgcolor='#0A2640' sx={{color:'white',paddingBottom:'3rem'}}
        className='landing-page-cont' width='1oo%' height={{sm:'100%', md:'84vh'}}>
          
            <Stack direction={{sm:"column",md:"row"}} width='100%' height='80%' spacing={6} >  
               <Stack spacing={3} 
                     sx={{width:{sm:'90%', md:'50%'},height:'70%',paddingLeft:{sm:3 , md:0},}}>
                         <ScrollReveal.div className="center"> 
                            <ScrollReveal.h1 animation="slide-in-bottom" className="color-blue">
                            <Typography variant='h3'  fontFamily='Manrope'>
                                 Save time by building  fast with Boldo Template 
                                 </Typography>  
                                </ScrollReveal.h1>  
                            </ScrollReveal.div> 
                            <ScrollReveal.div className="center"> 
                                <ScrollReveal.p animation="fade-in"  className="color-blue">
                                <Typography fontFamily='Open Sans' variant='body1' >
                                     Funding handshake buyer business-to-business metrics iPad partnership. First mover advantage innovator success deployment non-disclosure.
                                 </Typography>
                                    </ScrollReveal.p>
                            </ScrollReveal.div>
                            <ScrollReveal.div className="center" style={{width:'70%'}}> 
                            <ScrollReveal.h1 animation="fade-in" className="color-blue">
                            <Stack spacing={2}  direction='row'>
                                     <Button  size='medium' variant="contained" sx={{'&:hover' :  {backgroundColor:  ' #0A2648',color:'white',},backgroundColor:'secondary.main' ,fontFamily:'Open Sans',borderRadius:'20px',color:'black',boxShadow:'none'}}>Buy template</Button>
                                     <Button  size='medium' variant="outlined"  sx={{borderRadius:'20px',px:5,color:'secondary.main','&:hover' :  {backgroundColor:  ' #65E4A3',fontFamily:'Open Sans',color:'white',}}} >explore</Button>
                                 </Stack> 
                                </ScrollReveal.h1>  
                            </ScrollReveal.div> 
                                
                     </Stack>   
                     <Stack width={10} height={20}></Stack>{/* this is just there to keep a gap idk it was a good solution at the time */}
                     <ScrollReveal.div style={{width:'70%',height:'100%'}} className="center"> 
                            <ScrollReveal.div animation="slide-in-right" className="color-blue">   
                     <Box sx={{width:'100%',height:'100%',paddingLeft:{sm:3 , md:0},paddingTop:{sm:0 , md:0}}}>
                      {statisticsIcon}  
                     </Box>
                                </ScrollReveal.div>
                        </ScrollReveal.div>
             </Stack>
            <Stack height='20%' width='100%'>
            <ScrollReveal.div className="fading-Icon" style={{width:'100%',height:'100%'}}   > 
                            <ScrollReveal.h1 animation="fade-in" style={{width:'100%',height:'100%'}} className="color-blue">
                            {fadingLogoIcon}
                                </ScrollReveal.h1>  
                            </ScrollReveal.div>
       
           
            </Stack>
        </Stack>
    )
}
